<template>
  <div v-html="formattedString"></div>
</template>

<script>
export default {
  props: {
    inputString: {
      type: String,
      required: true,
    },
  },
  computed: {
    formattedString() {
      // 
      const replacedString = this.inputString
          .replace(/\n/g, '<br>')
          .replace(/{/g, '&#123;')
          .replace(/}/g, '&#125;');

      // <pre></pre>
      return `<pre>${replacedString}</pre>`;
    },
  },
};
</script>
