<template>
    <div class="login_container">
        <div class="login_box">
            <el-form ref="loginFormRef" :rules="rules" :model="loginForm" class="login_form" label-width="0px">
            <el-form-item prop="username">
            <el-input v-model="loginForm.username" prefix-icon="el-icon-user" clearable></el-input>
            </el-form-item>
            <el-form-item prop="password">
            <el-input  v-model="loginForm.password"  prefix-icon="el-icon-lock" show-password></el-input>
            </el-form-item>
             <el-form-item class="btns">
            <el-button type="primary" @click="login"></el-button>
            <el-button type="info" @click="resetForm"></el-button>
            </el-form-item>
            </el-form>
        </div>    
    </div>    
</template>




<script>
export default {
    data() {
        return {
            loginForm : {
                username:"",
                password:""
        },
        rules :{
            username: [{ required: true, message: '', trigger: 'blur' }],
            password: [{ required: true, message: '', trigger: 'blur' }],
        }
        }
    },
    methods: {
        resetForm(){
            this.$refs.loginFormRef.resetFields();
        },
        login(){
            this.$refs.loginFormRef.validate(vail => {
                if(!vail) return;
                this.$axios.post("/login",this.loginForm).
                    then(res=>{
                        console.log(res);
                        if(res.data.status == 0){
                            return this.$notify({title: '',message: '',type: 'success'})
                        }else if(res.data.status == 2){
                            return this.$notify({title: '',message: '',type: 'error'})
                        }else if(res.data.status == 1){
                            return this.$notify({title: '',message: '',type: 'error'})
                        }
                        })
            })
        }
    }
    
}
</script>




<style  scoped>

.login_container {
    background-color: bisque;
    height: 100%;
}

.login_box {
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    width: 450px;
    height: 300px;
    background-color: #fff;
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}

.btns {
    display: flex;
    justify-content: flex-end;
}

.login_form {
    position: absolute;
    bottom: 0px;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
}
</style>